n .mainLeft .tabs li:last-child{margin-top:135px}.Main .mainLeft .desc{margin:219px 50px 50px 50px;max-width:560px}.Main .mainLeft .desc a{text-decoration:underline}.Main .mainLeft .desc a:hover{background:linear-gradient(to right, #0f9, #0fe);-webkit-background-clip:text;-webkit-text-fill-color:transparent}.Main .darkMode{position:fixed;top:50px;right:80px;font-size:2em}.Main .darkMode:hover{background:linear-gradient(to right, #0f9, #0fe);-webkit-background-clip:text;-webkit-text-fill-color:transparent}

html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;font-size:100%;vertical-align:baseline;box-sizing:border-box}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}ol,ul{list-style:none}a{color:inherit;text-decoration:none}blockquote,q{quotes:none}blockquote:before,blockquote:after,q:before,q:after{content:"";content:none}table{border-collapse:collapse;border-spacing:0}

